import React from 'react';

const PanelSpecial = ({ heading, items }) => (
  <div className="panel panel_special mblg">
    <div className="panel__heading">
      <h3 className="panel__title">{heading}</h3>
    </div>
    <div className="panel__body">
      <ul className="list-unstyled">
        {items.map((item) => (
          <li className="mbmd" key={item}>
            <a href="#" className="txt-body-color">{item}</a>
          </li>
        ))}
      </ul>
      <a href="#">Xem thm</a>
    </div>
  </div>
);

const BlogItems = [
  'Thanh tra b nhim b Trn V Qunh Anh: L nhiu sai st',
  'Samsung Galaxy S8 v S8+ ra mt vi mn hnh v cc, 4G tc  1 Gbps',
  'Ph ch tch phng ngh sinh, ch tch... lnh !',
  'Chi 1.500 USD/m2 mua nh Home City, v sao c dn vn bc xc?'
];
const Blog = () => (
  <PanelSpecial
    heading="Blog"
    items={BlogItems}
  />
);

const VocabBuilder = () => (
  <div className="panel panel_special mblg">
    <div className="panel__heading">
      <h3 className="panel__title">Vocab Builder</h3>
    </div>
    <div className="panel__body">
      <p>Word of the day:</p>
      <div className="txt-bold">Engineer</div>
      <p><em>(n.)</em> Suppose you meet a girl in a park. She's riding a bike. Taking off all her clothes she screams, "Take whatever you want!" If you take the bikes, you're an engineer</p>
      <a href="#">Xem thm</a>
    </div>
  </div>
);

const QuizTestItems = [
  'CR7 nhu my khng vui trc bc tng xu ca mnh',
  'Nam Du - im n ma h cho nhng k \'cung chn\'',
  'Bn trong cn h 20 triu USD gia lng New York',
  'Sn Tng M-TP b t sao chp  tng trang phc  Seoul HOT'
]
const QuizTest = () => (
  <PanelSpecial
    heading="Quiz/Test"
    items={QuizTestItems}
  />
);

const LiveSessionItems = [
  'CR7 nhu my khng vui trc bc tng xu ca mnh',
  'Nam Du - im n ma h cho nhng k \'cung chn\'',
  'Bn trong cn h 20 triu USD gia lng New York',
  'Sn Tng M-TP b t sao chp  tng trang phc  Seoul HOT'
]
const LiveSession = () => (
  <PanelSpecial
    heading="Live Session"
    items={LiveSessionItems}
  />
);

export const SideBar = () => (
  <aside>
    <Blog />
    <VocabBuilder />
    <LiveSession />
    <QuizTest />
  </aside>
);



